<template>
  <div id="app">
    <UIBlock :activate="uiblock" v-bind:class="{ visible: uiblock }"></UIBlock>
    <vue-event-calendar :title="title" :events="demoEvents"></vue-event-calendar>
  </div>
</template>

<script>
import UIBlock from '@/components/UIBlock'

export default {
  data () {
    return {
      title: 'PrÃ³ximas calouradas',
      demoEvents: [],
      uiblock: false
    }
  },
  components: { UIBlock },
  mounted () {
    this.$http.get('http://api.igoncalves.tk/api/list').then(response => {
      console.log(response.body)
      this.demoEvents = response.body
      this.uiblock = true
    }, response => {
      console.error(response)
    })
  }

}
</script>

<style>
#app, body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #fff;
  background: #560562;
}

.__vev_calendar-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: flex-start;
}

.__vev_calendar-wrapper .cal-wrapper .cal-header{
  background: #40034a !important
}

.__vev_calendar-wrapper .arrow-right.icon,.__vev_calendar-wrapper .arrow-left.icon {
  color: #fff;
}
.cal-wrapper{
  order: 0;
}

.events-wrapper{
  order: 1;
}

.visible {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 0.5s, opacity 0.5s linear;
}
</style>



